import React from "react";

export default function SubImages() {
  return (
    <div style={{ display: "flex", flexDirection: "row", gap: "30px" }}>
      <div>
        <img
          src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f3.jpg"
          alt="flowers"
          width="360px"
          height="420px"
        />
        <div style={{ paddingTop: "20px" }}>
          <div style={{ color: "grey", fontSize: "18px", fontWeight: "600" }}>
            Cactus Plant
          </div>
          <div
            style={{
              paddingTop: "10px",
              fontSize: "22px",
              fontWeight: "700",
            }}
          >
            $40
          </div>
        </div>
      </div>
      <div>
        <div style={{ position: "relative" }}>
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f1.jpg"
            alt="flowers"
            width="360px"
            height="420px"
          />
          <div
            style={{
              position: "absolute",
              right: "20px",
              top: "30px",
              padding: "1rem 3rem 1rem 3rem",
              backgroundColor:'#000',
              color:'#fff',
              fontSize:'16px',
              fontWeight: '500'
            }}
          >
            Sold Out
          </div>
        </div>
        <div style={{ paddingTop: "20px" }}>
          <div style={{ color: "grey", fontSize: "18px", fontWeight: "600" }}>
            Bedroom Plant
          </div>
          <div
            style={{
              paddingTop: "10px",
              fontSize: "22px",
              fontWeight: "700",
            }}
          >
            $60
          </div>
        </div>
      </div>
    </div>
  );
}
