import React from "react";
import SubFeaturesPage from "../subfeatures";

export default function FeaturesPage() {
  return (
    <div
      style={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <div
        style={{
          width: '60%',  
          fontSize: "36px",
          fontWeight: "500",
          textAlign: "center",
          padding: "7rem 0rem 5.4rem 0rem",
        }}
      >
        Love and work are to people what water and sunshine are to plants
      </div>
      <SubFeaturesPage/>
    </div>
  );
}
