import React from "react";
import Paper from "@mui/material/Paper";
import InputBase from "@mui/material/InputBase";
import { Button } from "@mui/material";

export default function SearchBarN() {
  return (
    <Paper
      component="form"
      sx={{
        p: "0px 0px",
        display: "flex",
        alignItems: "center",
        width: "70%",
        border: "0px",
        borderRadius: "0px",
        boxShadow: "none",
        backgroundColor: "#d3d3d3",
      }}
    >
      <InputBase
        sx={{
          ml: 1,
          flex: 1,
          fontSize: "17px",
          fontWeight: "700",
          color: "#000",
        }}
        placeholder="Enter your email here"
      />
      <Button
        style={{
          fontSize: "14px",
          letterSpacing: "1.2px",
          fontWeight: "600",
          backgroundColor: "#333",
          color: "#fff",
          padding: "11px 38px",
          border: "0px",
          borderRadius: "0px",
        }}
      >
        Subscribe
      </Button>
    </Paper>
  );
}
