import React from "react";
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import "./navbar.scss";

export default function NavBar() {
  return (
    <div className="navbar-container">
      <div className="main-logo">Rad Plants</div>
      <div className="navbar-container" style={{gap:'40px'}}>
        <div className="main-links">Home</div>
        <div className="main-links">Catalogue</div>
        <div className="main-links">Contact</div>
      </div>
      <div className="navbar-container" style={{gap:'40px'}}>
        <div className="main-links"><SearchIcon sx={{fontSize:'32px'}}/></div>
        <div className="main-links"><ShoppingCartIcon sx={{fontSize:'32px'}}/></div>
      </div>
    </div>
  );
}
