import React from "react";
import SwiperCard from "../swipercard";

export default function CategoriesPage() {
  return (
    <div
      style={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: 'column'
      }}
    >
      <div
        style={{
          fontSize: "36px",
          fontWeight: "500",
          textAlign: "center",
          padding: "7rem 0rem 5.4rem 0rem",
        }}
      >
        Categories
      </div>
      <SwiperCard/>
    </div>
  );
}
