import { Button, Grid } from "@mui/material";
import React from "react";
import SubImages from "../subimages";

export default function SubFeaturesPage() {
  return (
    <Grid
      container
      style={{
        width: "100%",
        display: "flex",
        flexDirection: "row"
      }}
    >
      <Grid item xs={4}>
        <div
          style={{
            fontSize: "36px",
            fontWeight: "500",
            paddingBottom: "20px",
          }}
        >
          Featured
        </div>
        <div
          style={{
            width: "75%",
            fontSize: "20px",
            fontWeight: "500",
            paddingBottom: "20px",
            lineHeight: "22px",
          }}
        >
          Our plants are 100% organic, we don't use pesticides or harmful
          chemicals.
        </div>
        <div
          style={{
            width: "100%",
            fontSize: "20px",
            fontWeight: "500",
            paddingBottom: "20px",
            lineHeight: "22px",
          }}
        >
          But please don't eat them
        </div>
        <div>
          <Button
            style={{
              color: "#000",
              backgroundColor: "#d3d3d3",
              fontSize: "16px",
              fontWeight: "600",
              padding: "1rem 2rem",
              borderBottom: "2px solid #000",
              borderRadius: "0px",
              textTransform : 'none'
            }}
          >
            Shop all Favourites
          </Button>
        </div>
      </Grid>
      <Grid item xs={8}>
        <SubImages />
      </Grid>
    </Grid>
  );
}
