import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/pagination";
import "./swipercard.scss";
import { FreeMode } from "swiper";

export default function SwiperCard() {
  return (
    <>
      <Swiper
        slidesPerView={3}
        spaceBetween={30}
        freeMode={true}
        modules={[FreeMode]}
        className="mySwiper"
      >
        <SwiperSlide className="swiper-card" style={{marginLeft:'6rem'}}>
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f6.jpg"
            alt="plants"
            className="card-c-image"
          />

          <div className="card-c-subtext">Jungle Plants</div>
        </SwiperSlide>
        <SwiperSlide className="swiper-card">
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f8.jpg"
            alt="plants"
            className="card-c-image"
          />

          <div className="card-c-subtext">Outdoor Plants</div>
        </SwiperSlide>
        <SwiperSlide className="swiper-card">
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f1.jpg"
            alt="plants"
            className="card-c-image"
          />

          <div className="card-c-subtext">Bedroom Plants</div>
        </SwiperSlide>
        <SwiperSlide className="swiper-card">
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f2.jpg"
            alt="plants"
            className="card-c-image"
          />

          <div className="card-c-subtext">Garden Plants</div>
        </SwiperSlide>
        <SwiperSlide className="swiper-card">
          <img
            src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f3.jpg"
            alt="plants"
            className="card-c-image"
          />

          <div className="card-c-subtext">Cactus Plants</div>
        </SwiperSlide>
      </Swiper>
    </>
  );
}
