import { Button } from "@mui/material";
import React from "react";

export default function SContent() {
  return (
    <div
      style={{
        paddingTop: "30px",
        width: "100%",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        textAlign: "center",
        alignItems: "center",
      }}
    >
      <div style={{ width: "56%", fontSize: "24px" }}>
        Beautiful plants that encourage you to get creative.
      </div>
      <div style={{ paddingTop: "40px" }}>
        <Button
          style={{
            padding: "1rem 6rem",
            backgroundColor: "#222",
            fontSize: "18px",
            color: "#FFF",
            letterSpacing: "1.2px",
          }}
        >
          Shop Now
        </Button>
      </div>
      <div
        style={{
          paddingTop: "40px",
        }}
      >
        <img
          src="https://raw.githubusercontent.com/raviprakash11/pics/master/flowers/f7.jpg"
          alt="flowers"
          style={{ height: "650px", width: "100%" }}
        />
      </div>
    </div>
  );
}
