import { FormControl, MenuItem, Select } from "@mui/material";
import React from "react";
import "./mainfooter.scss";

export default function MainFooter() {
  const [age, setAge] = React.useState("");

  const handleChange = (event) => {
    setAge(event.target.value);
  };
  return (
    <>
      <div className="main-ft-cont">
        <div>
          <div className="main-ft-title">Rad Plants</div>
          <div>
            <FormControl
              sx={{
                m: 1,
                minWidth: 120,
                backgroundColor: "#d3d3d3",
                border: "0px",
                borderRadius: "0px",
              }}
            >
              <Select
                value={age}
                onChange={handleChange}
                displayEmpty
                inputProps={{ "aria-label": "Without label" }}
              >
                <MenuItem value="">
                  <div>USD</div>
                </MenuItem>
                <MenuItem value={10}>AUD</MenuItem>
              </Select>
            </FormControl>
          </div>
        </div>
        <div>
          <div className="main-ft-heading">Connects</div>
          <div className="main-ft-links">Instagram</div>
          <div className="main-ft-links">Facebook</div>
          <div className="main-ft-links">Pinterest</div>
          <div className="main-ft-links1">YouTube</div>
        </div>
        <div>
          <div className="main-ft-heading">Resources</div>
          <div className="main-ft-links">Return Policy</div>
          <div className="main-ft-links">Track An Order</div>
          <div className="main-ft-links">FAQs</div>
          <div className="main-ft-links1">Privacy Policy</div>
        </div>
        <div>
          <div className="main-ft-heading">About</div>
          <div className="main-ft-links">Our Story</div>
          <div className="main-ft-links">Careers</div>
          <div className="main-ft-links1">Press</div>
        </div>
      </div>
      <div
        style={{
          paddingTop: "60px",
          paddingBottom: "50px",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          width: "94%",
          fontSize: "18px",
          fontWeight: "600",
          color: "#d3d3d3",
        }}
      >
        Developed by NovaEnergon
      </div>
    </>
  );
}
