import React from "react";
import SearchBarN from "../searchbar";

export default function NewsLetter() {
  return (
    <div
      style={{
        paddingTop: "5.4rem",
        width: "100%",
        display: "flex",
        justifyContent: "center",
        flexDirection: "column",
        textAlign: "center",
        alignItems: "center",
      }}
    >
      <div style={{ width: "45%", fontSize: "30px", fontWeight:'500', paddingBottom:'40px' }}>
        Get 15% off on your next order, Subscribe to our Newsletter
      </div>
      <SearchBarN/>
    </div>
  );
}
